<template>
  <div style="background:white;height:100%;">
    <div class="SettingHeader">
      <van-nav-bar
        :fixed="true"
        title="关于我们"
        @click-left="()=>{
          $router.go(-1)
         }"
        left-arrow
        class="backgroundNaviBar"
        z-index="999"
        :border="false"
      >
        <!-- <van-icon name="filter-o" slot="right" size="20" /> -->
      </van-nav-bar>
    </div>
    <div class="settingContent">
      <div class="settingImage">
        <img src="static/img/settingLogo.png" alt />
      </div>
      <div class="edition">
        <span>版本号：</span>
        <span>{{version}}</span>
      </div>
      <div class="contentEdit">
        <div>
          <p>宝安区治水提质动态水质在线监测系统是针对宝安区内水污染源监测开发的在线监测数据服务系统，该系统旨在为环保管理者、水站管理者、水站运维人员提供智能、高效、便捷的管理功能，及时了解辖区内水质状况。</p>
        </div>
      </div>
      <div class="iphone">
        <div>
          <span>客服电话：</span>
          <span @click="Callme('0755-22672445')" style="color:#43adfd">0755-22672445</span>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { Dialog } from "vant";
export default {
  name: "aboutMen",
  data() {
    return {
      version: localStorage.getItem("version")
    };
  },
  methods: {
    Callme(number) {
      var _this = this;
      this.showMapData = false;
      Dialog.confirm({
        message: number,
        className: "dialogContent"
      })
        .then(() => {
          // on confirm
          window.location.href = "tel://" + number;
        })
        .catch(() => {
          // on cancel
          _this.showMapData = true;
        });
    }
  },
  created() {
    if (
      localStorage.getItem("version") === null ||
      localStorage.getItem("version") === undefined ||
      localStorage.getItem("version") === ""
    ) {
      cordova.getAppVersion.getVersionNumber().then(function(version) {
        //获取当前app的版本号；
        // $scope.now_version = version;
        localStorage.setItem("version", version);
      });
    }
    this.version = localStorage.getItem("version");
  }
};
</script>
<style scoped>
.settingImage img {
  width: 150px;
  height: auto;
}
.settingContent {
  width: 100%;
  text-align: center;
  padding-top: 120px;
}
.edition {
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgb(167, 167, 167);
}
.contentEdit {
  margin-top: 70px;
  padding: 0 30px;
  text-align: left;
}
.contentEdit p {
  line-height: 25px;
  /* text-decoration: 8px; */
  /* text-indent:30px; */
}
.iphone {
  text-align: left;
  padding: 0 30px;
}
</style>



